
@media screen and (max-width:1200px) {
    .header {
        &_wrap {
            align-items: flex-start;
        }
        &_right {
            margin-top: 5px;
        }
        &_left {
            &--ul {
                position: absolute;
                flex-direction: column;
                background: #fff;
                width: 130px;
                padding: 10px;
                margin: 0;
                border-radius: 5px;
               display: none;
                transition: .3s all;
                &.active {
                    display: block;
                    z-index: 10;
                }
            }
            &--li {
                margin: 10px 0px;
            }
            &--a {
                color: #364161;
                &:hover {
                    color: #364161;
                }
            }
        }
    }
    .btn_mnu {
        width: 50px;
        height: 50px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background-color: transparent;
        cursor: pointer;
        position: relative;
    }
    .btn_row {
        height: 2px;
        width: 60%;
        margin: 2px 0;
        background-color: #fff;
        position: relative;
        -webkit-transition: all .5s;
        -o-transition: all .5s;
        transition: all .5s;
    }
    .btn_mnu.active .btn_row:first-child {
        -webkit-transform: rotate(-135deg);
        -ms-transform: rotate(-135deg);
        -o-transform: rotate(-135deg);
        transform: rotate(-135deg);
        top: 6px;
    }
    .btn_mnu.active .btn_row:last-child {
        -webkit-transform: rotate(-225deg);
        -ms-transform: rotate(-225deg);
        -o-transform: rotate(-225deg);
        transform: rotate(-225deg);
        top: -6px;
    }
    .btn_mnu.active .btn_row:nth-child(2) {
        opacity: 0;
    }
    .intro {
        &_img {
            left: 0px;
        }
        &_h1 {
            min-width: inherit;
        }
    }
}

@media screen and (max-width:1170px) {
    .header_wrap.default {
        position: fixed;
        top: 0; left: 0;
        max-width: inherit;
        width: 100%;
        background: #fefefe;
        box-shadow: 0 1px 10px 0px rgba(0,0,0,.2);
        z-index: 101;
    }
    .header_wrap.default .header_container {
        background: #fefefe;
    }
    .header_wrap.default .btn_row {
        background-color: #000;
    }
    .header_wrap.default .header_left--logo p {
        color: #000;
    }
}
@media screen and (max-width:900px) {
    .intro {
        &_img {
            display: none;
        }
    }
    .header_left--logo {
        p {
            display: none;
        }
        img {
            margin-left: 7px;
        }
    }
    .happy {
        min-height: 400px;
        background-size: contain;
    }
    .happy_pin {
        display: none !important;
    }
    .happy_text {
        position: static;
        opacity: 1;
        margin: 35px auto 0;
        box-shadow: 5px -5px 25px rgba(0,0,0,.2);
    }
}
@media screen and (max-width:600px) {
    .intro_plus {
        display: none;
    }
    .intro {
        height: 650px;
    }
}
@media screen and (max-width:420px) {
    .intro {
        &_h1 {
            font-size: 36px;
        }
        &_subt {
            margin-bottom: 50px;
        }
        &_social--item {
            margin-left: 20px;
        }
    }
    .footer_right--h3 {
        font-size: 18px;
    }
    .footer_right--input,
    .doc_right--input{
        font-size: 14px;
        margin: 15px 0;
    }
    .footer_right--btn,
    .doc_right--btn{
        margin-top: 10px;
        padding: 0 25px;
        font-size: 16px;
        height: 55px;
    }
    .btn-up {
        display: none;
    }
}
@media screen and (max-width:768px) {
    .way_top img {
        display: none;
    }
    .way_h2,
    .way_subt,
    .how_h2,
    .how_subt,
    .need_h2,
    .need_subt,
    .happy_h2,
    .happy_subt
    {
        text-align: center;
        display: block;
    }
    .way_tabs {
        flex-wrap: wrap;
        justify-content: center;
    }
    .way_tab {
        margin: 10px;
    }
    .way_item {
        flex-wrap: wrap;
        justify-content: center;
        &--item {
            margin: 20px;
        }
    }
    .banner_h2 {
        font-size: 24px;
    }
    .need_content {
        flex-wrap: wrap;
        justify-content: center;
    }
    .need_item {
        margin: 20px;
    } 
    .doc_content {
        flex-wrap: wrap;
        justify-content: center;
        margin-top: 25px;
    }
    .doc_left {
        justify-content: center;
        text-align: center;
        &--item {
            width: auto;
            margin: 25px;
        }
        &--p {
            position: static;
        }
    }
    .doc_right {
        margin-top: 50px;
    }
    .footer_content {
        flex-wrap: wrap;
        justify-content: center;
    }
    .footer_left {
        order: 2;
        margin-top: 50px;
    }
    .footer_right {
        order: 1;
        position: static;
    }

}
@media screen and (max-width:600px) {
    .how_content {
        flex-wrap: wrap;
        justify-content: center;
    }
    .how_item {
        margin: 20px;
        width: 100%;
    }
    .free_btn {
        padding: 0 25px;
    }
    .doc_h2 {
        font-size: 26px;
    }
    .slick-slide img {
        display: inline;
    }
    .need_h4 {
        margin: 35px auto 0;
    }
    .slick-slide.happy_pin img {
        display: none;
    }
    .way_tab {
        font-size: 12px;
        width: auto;
        height: inherit;
        padding: 10px;
        margin: 0;
    }
    .way_content {
        margin-top: 15px;
    }
} 