.container {
    width: 100%;
    max-width: 975px;
    margin: 0 auto;
    padding: 0 15px;
}

@gradient: linear-gradient(to top, #ffcc00 0%, #ffba02 28%, #fda60b 50%, #ffba02 72%, #ffcc00 100%);

.btn() {
    background-image: @gradient;
    border: 2px solid #ffcc00;
    border-radius: 19px;
    text-align: center;
    transition: .3s all;
    cursor: pointer;
}
.hover() {
    background-image: none;
}

.btn-up {
cursor: pointer;
position: fixed;
bottom: -50px;
right: 100px;
width: 50px;
height: 50px;
border-radius: 50%;
border: 2px solid #eee;
background: #eee;
z-index: 100;
outline: 0!important;
-webkit-transition: .4s;
-o-transition: .4s;
transition: .4s;
}
.btn-up::after {
content: '';
width: 10px;
height: 10px;
border-top: 2px solid #00a1eb;
border-left: 2px solid #00a1eb;
-webkit-transform: rotate(45deg);
-ms-transform: rotate(45deg);
transform: rotate(45deg);
position: absolute;
top: 4px; bottom: 0;
right: 0; left: 0;
margin: auto;
}
.btn-up:hover::before {
content: "";
position: absolute;
border: 1px solid #eee;
left: -10px; right: -10px;
top: -10px; bottom: -10px;
border-radius: 50%;
-webkit-animation: pulse 1.8s linear infinite;
animation: pulse 1.8s linear infinite;
}
@-webkit-keyframes pulse {
0% {
-webkit-transform: scale(0.5);
transform: scale(0.5);
opacity: 0;
}
50% {
opacity: 1;
}
100% {
-webkit-transform: scale(1.2);
transform: scale(1.2);
opacity: 0;
}
}
@keyframes pulse {
0% {
-webkit-transform: scale(0.5);
transform: scale(0.5);
opacity: 0;
}
50% {
opacity: 1;
}
100% {
-webkit-transform: scale(1.2);
transform: scale(1.2);
opacity: 0;
}
}
.btn-up.active {
bottom: 50px;
}


.overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .8);
    display: none;
    z-index: 999;
}

.popup {
    position: fixed;
    width: 400px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 40px;
    box-sizing: border-box;
    background-color: #fff;
    color: #333;
    text-align: center;
    border-radius: 5px;

    &_title {
        font-size: 30px;
        .bold();
        padding: 10px 0;
    }
    &_descr {
        margin: 10px auto 25px;
    }
    &_input {
        display: block;
        margin: 20px auto;
        height: 42px;
        border-radius: 18px;
        background-color: #cecece;
        padding: 0 15px;
        width: 80%;
        color: #333;
        &::placeholder {
            color: #333;
        }
    }
    &_btn {
        .btn();
        .bold();
        display: block;
        width: 80%;
        font-size: 18px;
        text-transform: uppercase;
        color: #333;
        text-align: center;
        border-radius: 34px;
        text-align: center;
        height: 60px;
        line-height: 60px;
        margin: auto;
        &:hover {
            .hover();
        }
    }
}


.close-popup {
    position: absolute;
    top: 15px;
    right: 15px;
    width: 23px;
    height: 23px;
    cursor: pointer;
}

.close-popup:before {
    content: '';
    background-color: #000;
    position: absolute;
    height: 1px;
    width: 31px;
    top: 11px;
    left: -4px;
    transform: rotate(-45deg);
}

.close-popup:after {
    content: '';
    background-color: #000;
    position: absolute;
    height: 1px;
    width: 31px;
    top: 11px;
    transform: rotate(45deg);
    left: -4px;
}

@media screen and (max-width: 768px) {
    .popup {
        width: 100%;
    }
}
.slick-next {
    right: 0px;
    z-index: 100;
    &::before {
        content: '';
        position: absolute;
        top: 0; left: 0;
        transform: rotate(180deg);
        background: url(../img/last-track-left-arrow.png) no-repeat;
        width: 24px;
        height: 24px;
        opacity: 1;
    }
}
.slick-prev {
    z-index: 100;
    left: 0px;
    &::before {
        content: '';
        position: absolute;
        top: 0; left: 0;
        background: url(../img/last-track-left-arrow.png) no-repeat;
        width: 24px;
        height: 24px;
        opacity: 1;
    }
}

.fancybox-content {
    max-width: 800px;
    padding: 20px 0px 25px;
}
.visa-popup {
    &--top {
        display: flex;
        align-items: center;
        justify-content: center;
        img {
            height: 35px;
            width: 35px;
        }
    }
    &--h3 {
        .bold();
        font-size: 18px;
        margin-left: 10px;
    }
    &--table {
        margin-top: 25px;
        tr:nth-child(even) {
            background-color: #eee;
        }
        td {
            vertical-align: middle;
            padding: 7px;
        }
        
    }
    &--head {
        width: 30%;
        p {
            text-align: right;
            text-transform: uppercase;
            .medium();
        }
    }
    &--content {
        width: 30%;
        
        p {
            text-align: center;
        }
    }
}
@media screen and (max-width:600px) {
    .visa-popup {
        &--table {
            margin-top: 25px;
            tr:nth-child(even) {
                background-color: #eee;
            }
            td {
                vertical-align: middle;
                padding: 5px;
            }

        }
        &--head {
            width: 25%;
            p {
               font-size: 11px;
            }
        }
        &--content {
            width: 25%;

            p {
                font-size: 14px;
            }
        }
    }
}