.need {
    margin-top: 80px;
    &_h2 {
        color: #feba01;
        font-size: 30px;
        text-transform: uppercase;
        background: linear-gradient(to top, #ffcc00 0%, #ffba02 28%, #fda60b 50%, #ffba02 72%, #ffcc00 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        display: table;
        .bold();
    }
    &_subt {
        margin-top: 5px;
        font-size: 17px;
        text-transform: uppercase;
        .medium();
    }
    &_content {
        display: flex;
        justify-content: space-between;
        margin-top: 50px;
    }
    &_item {
        text-align: center;
        img {
            height: 120px;
        }
    }
    &_h4 {
        margin-top: 50px;
        max-width: 180px;
    }
    
}
