.footer {
    margin-top: 35px;
    &_content {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &_left {
        position: relative;
        top: 55px;
    }
    &_right {
        margin-top: 50px;
        position: relative;
        top: 55px;
        &--h3 {
            max-width: 350px;
            text-align: center;
            color: #040404;
            font-size: 22px;
            margin-bottom: 10px;
            .bold();
            span {
                color: #feba01;
                background: linear-gradient(to top, #ffcc00 0%, #ffba02 28%, #fda60b 50%, #ffba02 72%, #ffcc00 100%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
        }
        &--form {
            display: flex;
            flex-direction: column;
        }
        &--input {
            margin: 20px 0;
            height: 42px;
            border-radius: 18px;
            background-color: #cecece;
            padding: 0 15px;
        }
        &--btn {
            .btn();
            cursor: pointer;
            margin-top: 20px;
            height: 67px;
            border-radius: 34px;
            font-size: 24px;
            color: #141414;
            padding: 0 45px;
            .bold();
            &:hover {
                .hover();
            }
        }
    }
}
.bottom { 
    margin-top: 50px;
    background-color: #333;
    color: #f0f0f0;
    padding: 50px 0 25px;
    &_content {
        display: flex;
        justify-content: space-between;  
    }
    &_left {
        width: 55%;
    }
    &_right {
        width: 35%;
        &--h3 {
            .bold();
            font-size: 32px;
        }
        &--ul {
            margin-top: 30px;
        }
        &--li {
            margin: 10px 0;
            display: flex;
            justify-content: space-between;
            a {
                .bold();
            }
        }
    }
    .intro_social {
        margin-top: 50px;
        justify-content: center;
    }
}
@media screen and (max-width:930px) {
    .bottom_content {
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
    }
    .bottom_left {
        margin-top: 35px;
        order: 2;
        width: 100%;
        overflow: hidden;
    }
    .bottom_right {
        width: 100%;
        order: 1;
        padding-right: 20px;
        &--ul {
            margin: 30px auto 0;
        }
        &--li {
            justify-content: center;
        }
    }
    .bottom .header_right--callback {
        margin: 10px auto 0;
    }
}