.doc {
    margin-top: 70px;
    &_h2 {
        .bold();
        color: #151515;
        font-size: 33px;
        text-align: center;
        span {
            color: #feba01;
            background: linear-gradient(to top, #ffcc00 0%, #ffba02 28%, #fda60b 50%, #ffba02 72%, #ffcc00 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
    }
    &_content {
        margin-top: 75px;
        display: flex;
    }
    &_left {
        display: flex;
        flex-wrap: wrap;
        position: relative;
        &--item {
            width: 50%;
            margin: 25px 0;
        }
        &--h4 {
            margin-top: 5px;
            max-width: 160px;
            color: #0b0b0b;
            font-size: 16px;
            .bold();
        }
        &--p {
            position: absolute;
            bottom: 10px; left: 20px;
            color: #939393;
            font-size: 14px;
            .medium();
        }
    }
    &_right {
        margin-top: 5px;
        &--h3 {
            max-width: 350px;
            text-align: center;
            color: #151515;
            font-size: 16px;
            margin-bottom: 10px;
            .medium();
        }
        &--form {
            display: flex;
            flex-direction: column;
        }
        &--input {
            margin: 20px 0;
            height: 42px;
            border-radius: 18px;
            background-color: #cecece;
            padding: 0 15px;
        }
        &--btn {
            .btn();
            cursor: pointer;
            margin-top: 20px;
            height: 63px;
            font-size: 18px;
            color: #141414;
            border-radius: 34px;
            .bold();
            &:hover {
                .hover();
            }
        }
    }
}