.popup {
  position: fixed;
  width: 400px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 40px;
  box-sizing: border-box;
  background-color: #fff;
  color: #333;
  text-align: center;
  border-radius: 5px;
}

.popup_title {
  font-size: 30px;
  font-family: 'MullerBold', 'Arial', sans-serif;
  padding: 10px 0;
}

.popup_descr {
  margin: 10px auto 25px;
}

.popup_input {
  display: block;
  margin: 20px auto;
  height: 42px;
  border-radius: 18px;
  background-color: #cecece;
  padding: 0 15px;
  width: 80%;
  color: #333;
}

.popup_input::placeholder {
  color: #333;
}

.popup_btn {
  background-image: linear-gradient(to top, #ffcc00 0%, #ffba02 28%, #fda60b 50%, #ffba02 72%, #ffcc00 100%);
  border: 2px solid #ffcc00;
  border-radius: 19px;
  transition: .3s all;
  cursor: pointer;
  font-family: 'MullerBold', 'Arial', sans-serif;
  display: block;
  width: 80%;
  font-size: 18px;
  text-transform: uppercase;
  color: #333;
  border-radius: 34px;
  text-align: center;
  height: 60px;
  line-height: 60px;
  margin: auto;
}

.popup_btn:hover {
  background-image: none;
}

.close-popup {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 23px;
  height: 23px;
  cursor: pointer;
}

.close-popup:before {
  content: '';
  background-color: #000;
  position: absolute;
  height: 1px;
  width: 31px;
  top: 11px;
  left: -4px;
  transform: rotate(-45deg);
}

.close-popup:after {
  content: '';
  background-color: #000;
  position: absolute;
  height: 1px;
  width: 31px;
  top: 11px;
  transform: rotate(45deg);
  left: -4px;
}

.slick-next {
  right: 0px;
  z-index: 100;
}

.slick-next::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  transform: rotate(180deg);
  background: url(../img/last-track-left-arrow.png) no-repeat;
  width: 24px;
  height: 24px;
  opacity: 1;
}

.slick-prev {
  z-index: 100;
  left: 0px;
}

.slick-prev::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background: url(../img/last-track-left-arrow.png) no-repeat;
  width: 24px;
  height: 24px;
  opacity: 1;
}

