@media screen and (max-width: 1200px) {
  .header_wrap {
    align-items: flex-start;
  }

  .header_right {
    margin-top: 5px;
  }

  .header_left--ul {
    position: absolute;
    flex-direction: column;
    background: #fff;
    width: 130px;
    padding: 10px;
    margin: 0;
    border-radius: 5px;
    display: none;
    transition: .3s all;
  }

  .header_left--ul.active {
    display: block;
    z-index: 10;
  }

  .header_left--li {
    margin: 10px 0px;
  }

  .header_left--a {
    color: #364161;
  }

  .header_left--a:hover {
    color: #364161;
  }

  .btn_mnu {
    width: 50px;
    height: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    cursor: pointer;
    position: relative;
  }

  .btn_row {
    height: 2px;
    width: 60%;
    margin: 2px 0;
    background-color: #fff;
    position: relative;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
  }

  .btn_mnu.active .btn_row:first-child {
    -webkit-transform: rotate(-135deg);
    -ms-transform: rotate(-135deg);
    -o-transform: rotate(-135deg);
    transform: rotate(-135deg);
    top: 6px;
  }

  .btn_mnu.active .btn_row:last-child {
    -webkit-transform: rotate(-225deg);
    -ms-transform: rotate(-225deg);
    -o-transform: rotate(-225deg);
    transform: rotate(-225deg);
    top: -6px;
  }

  .btn_mnu.active .btn_row:nth-child(2) {
    opacity: 0;
  }

  .intro_img {
    left: 0px;
  }

  .intro_h1 {
    min-width: inherit;
  }
}

