.way {
    margin-top: 100px;
    color: #141414;
    &_top {
        position: relative;
        img {
            position: absolute;
            right: 100px;
            top: -20px;
        }
    }
    &_h2 {
        color: #feba01;
        font-size: 30px;
        text-transform: uppercase;
        background: linear-gradient(to top, #ffcc00 0%, #ffba02 28%, #fda60b 50%, #ffba02 72%, #ffcc00 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        display: table;
        .bold();
    }
    &_subt {
        margin-top: 5px;
        font-size: 17px;
        text-transform: uppercase;
        .medium();
    }
    &_bottom {
        margin-top: 40px;
    }
    &_tabs {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &_tab {
        cursor: pointer;
        text-align: center;
        font-size: 17px;
        text-transform: uppercase;
        transition: .3s all;
        margin-left: 20px;
        border-radius: 5px;
        width: 161px;
        height: 75px;
        display: flex;
        align-items: center;
        justify-content: center;
        .medium();
        &:first-child {
            margin-left: 0;
        }
        &.active {
            background-color: #797979;
            background-image: linear-gradient(to top, #ffcc00 0%, #ffba02 28%, #fda60b 50%, #ffba02 72%, #ffcc00 100%);
        }
    }
    &_content {
        margin-top: 25px;
    }
    &_item {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        &--item {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            margin: 20px;
            .two {
                height: 35px;
                width: 35px;
                margin: 15px auto 10px;
            }
        }
        &--h3 {
            font-size: 25px;
            .bold();
        }
        &--btn {
            .btn();
            margin-top: 15px;
            width: 150px;
            height: 40px;
            border-radius: 19px;
            line-height: 40px;
            font-size: 16px;
            .bold();
            &:hover {
                .hover();
            }
        }
    }
}