.intro {
    background: url(../img/intro.jpg) no-repeat;
    height: 729px;
    background-size: cover;
    background-position: center;
    color: #fff;
    &_wrap {
        display: flex;
    }
    &_content {
        padding-top: 180px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    &_img {
        position: relative;
        top: 95px; left: 50px;
    }
    &_h1 {
        display: inline-block;
        font-size: 42px;
        text-transform: uppercase;
        min-width: 550px;
        .bold();
    }
    &_subt {
        display: inline-block;
        margin: 30px 0 50px;
        font-size: 19px;
    }
    &_btn {
        .btn();
        .bold();
        display: inline-block;
        font-size: 15px;
        text-transform: uppercase;
        color: #333;
        text-align: center;
        border-radius: 34px;
        text-align: center;
        height: 60px;
        line-height: 60px;
        padding: 0 65px;
        &:hover {
            .hover();
            color: #fff;
        }
    }
    &_plus {
        margin-top: 80px;
        display: flex;
        justify-content: space-between;
        &--item {
            display: flex;
            flex-direction: column;
            margin-left: 40px;
            &:first-child {
                margin-left: 0;
            }
        }
        &--title {
            background: linear-gradient(to top, #ffcc00 0%, #ffba02 28%, #fda60b 50%, #ffba02 72%, #ffcc00 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-size: 24px;
            .bold();
        }
        &--desc {
            margin-top: 5px;
            font-size: 15px;
            color: #f0f0f0;
        }
    }
    &_social {
        margin-top: 60px;
        display: flex;
        justify-content: space-between;
        &--item {
            display: flex;
            align-items: flex-start;
            margin-left: 30px;
            &:first-child {
                margin-left: 0;
            }
            p {
                margin-left: 10px;
                font-size: 15px;
                .bold();
            }
            img {
                
                width: 15px;
            }
        }
    }
}

.header {
    color: #fafafa;
    &_container {
        max-width: 1170px;
        width: 100%;
        margin: 0 auto;
        position: relative;
        padding: 0 15px;
    }
    &_wrap {
        position: absolute;
        top: 0; left: 0;
        display: flex;
        justify-content: space-between;
        max-width: 1060px;
        width: 100%;
        padding: 10px 15px 0;
        z-index: 11;
    }
    &_left {
        &--logo {
            display: flex;
            align-items: center;
            p {
                margin-left: 10px;
                font-size: 18px;
                text-transform: uppercase;
                .medium();
            }
        }
        &--ul {
            display: flex;
            margin-top: 20px;
        }
        &--li {
            margin: 0 10px;
        }
        &--a {
            .medium();
            color: #919191;
            font-size: 13px;
            text-transform: uppercase;
            transition: .3s all;
            &:hover {
                color: #fff;
            }
        }
    }
    &_right {
        display: flex;
        flex-direction: column;
        text-align: center;
        &--tel {
            .bold();
            color: #333;
            font-size: 16px;
            text-transform: uppercase;
        }
        &--callback {
            .bold();
            display: block;
            margin-top: 10px;
            padding: 0px 20px;
            color: #333;
            font-size: 15px;
            line-height: 37px;
            height: 39px;
            border-radius: 25px;
            background-color: #fbbc10;
            border: 2px solid #fbbc10;
            transition: .3s all;
            &:hover {
                background-color: transparent;
                color: #fff;
            }
        }
    }
}
