.happy {
    margin-top: 120px;
    background: url(../img/earth.png) no-repeat;
    min-height: 683px;
    background-position: center;
    &_h2 {
        color: #feba01;
        font-size: 30px;
        text-transform: uppercase;
        background: linear-gradient(to top, #ffcc00 0%, #ffba02 28%, #fda60b 50%, #ffba02 72%, #ffcc00 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        display: table;
        .bold();
    }
    &_subt {
        margin-top: 5px;
        font-size: 17px;
        text-transform: uppercase;
        .medium();
    }
    &_content {
        position: relative;
    }
    &_pin {
        position: absolute;
        z-index: 10;
        cursor: pointer;
        &::after {
            content: '';
            position: absolute;
            top: 16px; left: 25px;
            background: url(../img/x.png) no-repeat;
            width: 6px;
            height: 7px;
            display: none;
        }
        &1 {
            top: 210px; left: 395px;
            &.active::after {
                display: block;
            }
        }
        &2 {
            top: 200px;
            left: 90px;
            &.active::after {
                display: block;
            }
        }
        &3 {
            top: 400px;
            left: 190px;
        }
        &4 {
            top: 170px;
            left: 480px;
        }
        &5 {
            top: 220px; left: 730px;
            &.active::after {
                display: block;
            }
        }
    }
    &_text {
        position: absolute;
        z-index: 11;
        cursor: pointer;
        top: 280px; left: 400px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 180px;
        height: 256px;
        box-shadow: -8px -6px 27px rgba(2, 3, 3, 0.43);
        background-color: #ffffff;
        opacity: 0;
        transition: .3s all;
        &1 {
            top: 280px; left: 340px;
        }
        &2 {
            top: 270px; left: 30px;
        }
        &5 {
            top: 290px; left: 670px;
        }
        &.active {
            opacity: 1;
        }
        &--top {
            padding: 10px 10px 0;
        }
        &--name {
            font-size: 12px;
            font-weight: bold;
            display: inline;
        }
        &--country {
            margin-left: 5px;
            display: inline;
            font-size: 12px;
            font-weight: bold;
            text-transform: uppercase;
            background: linear-gradient(to top, #ffcc00 0%, #ffba02 28%, #fda60b 50%, #ffba02 72%, #ffcc00 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
        &--desc {
           margin-top: 20px;
            padding: 0 10px;
            color: #858585;
            font-size: 12px;
            .medium();
        }
    }
}