.banner {
    background: url(../img/banner.png) no-repeat;
    background-position: center;
    height: 274px;
    &_content {
        position: relative;
        height: 274px;
        padding: 70px 50px 50px 0;
        color: #fff;
        max-width: 480px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    &_h2 {
        font-size: 28px;
        font-weight: bold;
        line-height: 29.01px;
        text-transform: uppercase;
    }
    &_p {
        font-size: 15px;
    }
    &_h4 {
        font-size: 17px;
        .medium();
    }
}

.how {
    margin-top: 70px;
    &_h2 {
        color: #feba01;
        font-size: 30px;
        text-transform: uppercase;
        background: linear-gradient(to top, #ffcc00 0%, #ffba02 28%, #fda60b 50%, #ffba02 72%, #ffcc00 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        display: table;
        .bold();
    }
    &_subt {
        margin-top: 5px;
        font-size: 17px;
        text-transform: uppercase;
        .medium();
    }
    &_content {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        margin-top: 25px;
    }
    &_item {
        margin: 25px 0;
        width: 45%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &--left {
            color: #feba01;
            font-size: 56px;
            text-transform: uppercase;
            background: linear-gradient(to top, #ffcc00 0%, #ffba02 28%, #fda60b 50%, #ffba02 72%, #ffcc00 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            display: table;
            .bold();
        }
        &--right {
            .medium();
            color: #474749;
            font-size: 14px;
            margin-left: 30px;
            line-height: 1.5;
        }
    }
}

.free {
    margin-top: 50px;
    text-align: center;
    &_h4 {
        .bold();
        color: #a91107;
        font-size: 20px;
        font-weight: bold;
        text-transform: uppercase;
    }
    &_p {
        color: #262628;
        font-size: 16px;
        .medium();
        display: block;
        margin: 5px auto 15px;
        line-height: 1.5;
    }
    &_btn {
        .btn();
        height: 67px;
        border-radius: 34px;
        line-height: 67px;
        padding: 0 65px;
        display: inline-block;
        .bold();
        &:hover {
            .hover();
        }
    }
}